<template>
  <imp-panel title="">
    <el-form ref="form" :model="form" label-width="180px" :rules="rules">
      <el-form-item label="" prop="oldPassword" :rules="[{ required: true, message: ''}]">
        <el-input v-model="form.oldPassword" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newPassword" :rules="[{ required: true, message: ''}]">
        <el-input v-model="form.newPassword" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newPassword2">
        <el-input v-model="form.newPassword2" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')"></el-button>
      </el-form-item>
    </el-form>
  </imp-panel>
</template>
<script>
  import * as api from "../api"
  import * as sysApi from '../services/sys'
  import  auth from '../common/auth'
  export  default{
    data(){
      var validatePass2 = (rule, value, callback) => {
      	if(!value){
      		callback(new Error('!'));
      	}else if (value !== this.form.newPassword) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        form: {
          oldPassword: '',
          newPassword: '',
          newPassword2: ''
        },
        rules: {
        	newPassword2: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
//  	
      onSubmit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
          	this.checkConfirm();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
//    
      checkConfirm(){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
        	this.modifyPwd();
        }).catch(() => {
          this.$message.info('');
        });
      },
//    
      modifyPwd(){
      	let item = window.localStorage.getItem("user-info");
      	if(!!item){
      		item = JSON.parse(item);
	        sysApi.modifyPwd({
	        	userId: item.userId,
	          oldPassword: this.form.oldPassword,
	          newPassword: this.form.newPassword
	        })
	        .then(res => {
	        	if(res && res.code=='0'){
	        		this.pwdSubmit();
	        	}else{
	        		this.$message.error(''+res.message);
	        	}
	        });
      	}else{
      		this.$message.error('');
      	}
      },
      pwdSubmit(){
        this.$alert(', ', '', {
          confirmButtonText: '',
          callback: action => {
		        auth.logout();
//		        this.$http.defaults.headers.common['authSid'] = '';
		        this.$router.push({path: '/login'});
          }
        });
      }
    }
  }
</script>



